.nav {
  position: fixed;
  top: 0;
  width: 100%;
  display: flex;
  background-color: #111;
  transition-timing-function: ease-in;
  transition: all 0.8s;
  color: aliceblue;
}
.netflix_logo {
  width: 7vw;
  object-fit: contain;
  padding-bottom: 20px;
  padding-left: 20px;
  padding-top: 16px;
}

.nav_trans {
  background-color: transparent;
  transition-timing-function: ease-in-out;
  transition: all 0.8s;
}
.nav_logoShow {
  display: none;
}
