.banner {
  color: white;
  object-fit: contain;
  margin-top: 48px;
}
.banner_content {
  margin-left: 30px;
  padding-top: 150px;
  height: 12vw;
  width: fit-content;
}
.banner_title {
  font-size: 3rem;
  padding-bottom: 0.3rem;
  font-weight: 800;
}
.banner_button {
  cursor: pointer;
  outline: none;
  border: none;
  border-radius: 0.2vw;
  color: #fff;
  background-color: rgb(51, 51, 51, 0.5);
  padding-left: 2rem;
  margin-right: 1rem;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  padding-right: 2rem;
}
.banner_descp {
  line-height: 1.3;
  padding-top: 1rem;
  max-width: 36rem;
  font-size: 1.1rem;
  height: 80px;
}
.banner_button:hover {
  color: black;
  background-color: aliceblue;
  transition: all 0.2s;
}
.banner--fadeBottom {
  height: 10rem;
  background-image: linear-gradient(
    180deg,
    transparent,
    rgba(37, 37, 37, 0.61),
    #111
  );
}
