.row_poster {
  object-fit: contain;
  width: 100%;
  max-height: 150px;
  margin-right: 10px;
  transition: transform 450ms;
}

.row_posters::-webkit-scrollbar {
  display: none;
}
.row_posters {
  display: flex;
  overflow-y: hidden;
  overflow-x: scroll;
  padding: 10px;
}

.row_poster:hover {
  transform: scale(1.05);
  backface-visibility: hidden;
}

.row_posterlarge {
  width: fit-content;
  max-height: 250px;
}

.row_posterlarge:hover {
  transform: scale(1.08);
  backface-visibility: hidden;
}

.row_title {
  padding-top: 0.5rem;
  padding-left: 0.5rem;
}
